import React, {useState} from 'react';
import scss from './MainMenu.module.scss';
import clsx from 'clsx';
import {PageContext} from '../../Context/PageContext';
import Svg from './Svg'

function MainMenu() {
    // const [openMenu, setOpenMenu] = useState(false)
    // const [hovering, setHovering] = useState('')

    const {
      pageNames,
      handleSetCurPage,
    } = React.useContext(PageContext) 

    const handlePathClick = (page) => {
        if(page!=='main') {
            handleSetCurPage(page)
        }
    }
  return (
    <div className={scss.MainMenu}>
      <Svg 
        className={scss.svg}
        handleClickEXP = {()=>handlePathClick(pageNames.EXP)}
        handleClickAG = {()=>handlePathClick(pageNames.AG)}
        handleClickNOKO = {()=>handlePathClick(pageNames.NOKO)}
        handleClickTABLE = {()=>handlePathClick(pageNames.TABLE)}
        handleClickTREE = {()=>handlePathClick(pageNames.TREE)}
      />
    </div>
  );
}

export default MainMenu;
